import React from 'react'
import { Route } from 'react-router-dom'
import AboutArmeniaAM from './pages/AM-Versions/AboutArmeniaAM.js'
import HomeAM from './pages/AM-Versions/HomepageAM.js'
import AboutTeamAM from './pages/AM-Versions/AboutTeamAM.js'
import AchievementsAM from './pages/AM-Versions/AchievementsAM.js'
import ErrorPageAM from './pages/AM-Versions/ErrorPageAm.js'
import AnnouncementsAM from './pages/AM-Versions/announcements.js'
import ContactUsAM from './pages/AM-Versions/ContactUsAM.js'
import EventsAM from './pages/AM-Versions/EventsAM.js'
import GalleryVideoAM from './pages/AM-Versions/GalleryVideoAM.js'
import GalleryAM from './pages/AM-Versions/GalleryAM.js'
import JobsAM from './pages/AM-Versions/JobsAM.js'
import NewsAM from './pages/AM-Versions/NewsAM.js'

function App() {
	return (
		<div>
			<Route path={"/"} exact component={HomeAM}></Route>
			<Route path={"/about-armenia-am"} component={AboutArmeniaAM}></Route>
			<Route path={"/about-team-am"} component={AboutTeamAM}></Route>
			<Route path={"/achievements-am"} component={AchievementsAM}></Route>
			<Route path={"/error-page-am"} component={ErrorPageAM}></Route>
			<Route path={"/announcements-am"} component={AnnouncementsAM}></Route>
			<Route path={"/contact-us-am"} component={ContactUsAM}></Route>
			<Route path={"/events-am"} component={EventsAM}></Route>
			<Route path={"/gallery-video-am"} component={GalleryVideoAM}></Route>
			<Route path={"/gallery-am"} component={GalleryAM}></Route>
			<Route path={"/jobs-am"} component={JobsAM}></Route>
			<Route path={"/news-am"} component={NewsAM}></Route>
		</div>
	);
}

export default App;
